$blue: #342dfc;

$mobile: 480px;
$tablet: 800px;
$laptop: 1024px;
$desktop: 1300px;

nav {
  ul {
    display: flex;
    li {
      list-style: none;
      padding: 17px;
      &:hover {
        cursor: pointer;
      }
      margin: 0 10px;
      a {
        text-decoration: none;
      }
    }
  }
}
.navbar_1 {
  color: white;
  ul {
    justify-content: flex-end;
    align-items: center;
  }
  a {
    color: white;
  }
}
.navbar_2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 10%;
  ul {
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }
  img {
    max-width: 50px;
    object-fit: contain;
    border-radius: 8px;
    position: relative;
    left: 0.5rem;
  }
  svg {
    display: none;
  }
}
.navbar_1,
.navbar_2 {
  max-width: 1300px;
  margin: 0 auto;
}

.menu {
  display: none;
  position: absolute;
  top: 2rem;
  left: 1.1rem;
  flex-direction: column;
  justify-content: space-between;
  width: 1.8rem;
  height: 1.5rem;
  span {
    background-color: black;
    height: 0.15rem;
    width: 100%;
    border-radius: 0.2rem;
  }
}
.menu:hover {
  cursor: pointer;
}
.navTab {
  display: none;
  height: 5rem;
  width: 100%;
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
}

.nav_cont {
  display: flex;
  flex-direction: column;
}
.navbar_wrapper {
  background-color: $blue;
}
.nav2_wrapper {
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
}

.sidebar {
  position: fixed;
  top: 3.7rem;
  left: 0;
  height: 100vh;
  background-color: white;
  width: 100%;
  display: none;
  z-index: 1;
  ul {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    li {
      width: 100%;
    }
  }
}

@media (max-width: $laptop) {
  .navbar_1 {
    display: none;
  }
  .navbar_2 {
    margin: 0;
    padding: 5px;
    ul {
      display: none;
      li {
        margin: 20px 0;
      }
    }
    img {
      left: 0.5rem;
    }
    svg {
      position: absolute;
      top: 15px;
      left: 15px;
      display: flex;
      &:hover {
        cursor: pointer;
      }
      transition: all 0.75s ease-in-out;
    }
  }

  nav {
    ul {
      li {
        margin: 10px 0;
      }
    }
  }
}
